.form {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 25px;
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
  &__city {
    display: flex;
    font-size: 12px;
    margin-bottom: 10px;
    align-items: center;
    justify-content: flex-end;
    font-weight: 300;
    width: 100%;
    span{
      font-weight: 300;
      font-size: 12px
    }
  }
  &__list-city{
    position: absolute;
    
  }
  &__list{
    position: relative;
  }
  &__point {
    width: 100%;
    font-weight: 300;
    display: flex;
    font-size: 12px;
    align-items: center;
    justify-content: flex-end;
    span{
      font-weight: 300;
      font-size: 12px;
    }
  }
  &__input {
    outline: none;
    min-width: 180px;
    width: 60%;
    border: 0;
    border-bottom: 1px solid #999999;
    margin-left: 8px;
    padding-right: 0px;
    &:disabled {
      background-color: transparent;
      border-bottom: 1px solid #d3d3d3;
    }
  }
}

@media (min-width: 768px) {
  .form {
    margin-bottom: 45px;
    &__city {
      font-size: 14px;
      span{
        font-size: 14px;
      }
    }
    &__point {
      font-size: 14px;
      span{
        font-size: 14px;
      }
    }
  }
}
